import { useInput } from 'hooks/useInput'
import { useUser } from 'hooks/useUser'

import { useHistory } from 'react-router-dom'

import Button from '../Assets/Button/Button'
import Header from '../Assets/Header/Header'
import Input from '../Assets/Input/Input'
import Message from '../Assets/Message/Message'
import ServiceResponse from '../Assets/ServiceResponse/ServiceResponse'

import Question from '../Question/Question'
import { headers } from './helper'

export default function Forgot({ onModalHandler, closeModal }) {
  const { reset: resetPet, ...pet } = useInput('text')
  const { reset: resetHero, ...hero } = useInput('text')
  const { reset, ...email } = useInput('email')
  const { recoverPassword, error, loading } = useUser()
  const { push } = useHistory()

  const onCancelHandler = () => onModalHandler('login')

  const onRecoverHandler = async (e) => {
    e.preventDefault()

    const recover = {
      field: email.value,
      responses: [pet.value, hero.value]
    }

    const token = await recoverPassword(recover)
    if (!token) return

    closeModal()
    push(`/reset/?token=${token}`)
  }

  return (
    <>
      <Header {...headers} />

      <form className="principal-form-container" onSubmit={onRecoverHandler}>
        <ServiceResponse response={error} />

        <Input {...email} placeholder="Email address" />

        <Question pet={pet} hero={hero} showTitle={false} />

        <Button
          title={loading ? 'Loading...' : 'Continue'}
          type="fill"
          isSubmit={true}
        />

        <Message title="Cancel" position="center" onClick={onCancelHandler} />
      </form>
    </>
  )
}
