import { useContext, useEffect } from 'react'
import { Redirect, useHistory } from 'react-router-dom'

import UserContext from 'context/userContext'

import Backward from 'components/Backward/Backward'
import Reports from 'components/Reports/Reports'
import Modal from 'components/Modal/Modal'

import { useModal } from 'hooks/useModal'
import { useReports } from 'hooks/useReports'

import Denied from '../Denied/Denied'
import classes from './Dashboard.module.css'
import Button from './NewGame/NewGame'
import { Url } from 'utils/url.params'

export default function Dashboard() {
  const { push } = useHistory()
  const { user, removeUser } = useContext(UserContext)
  const createModal = useModal('create')
  const initialOffset = Url('offset')
  const refreshModal = useModal('refresh')
  const { reports, loading, current, update, canRefresh } =
    useReports(initialOffset)

  useEffect(() => {
    const toggleToken = () => {
      if (canRefresh === null) {
        return
      }

      if (canRefresh['canRefresh']) {
        refreshModal.showCurrentModal()
        return
      }

      removeUser()
      alert('Your session has expired')
      push('/')
    }

    toggleToken()
  }, [canRefresh])

  if (!user) return <Redirect to="/" />

  if (!user['isAuthorized']) return <Denied />

  return (
    <>
      {!reports.authorized && <Redirect to="/unauthorized" />}

      {createModal.activeModal && (
        <Modal onClose={createModal.onClose}>{createModal.currentModal}</Modal>
      )}

      {refreshModal.activeModal && (
        <Modal onClose={refreshModal.onClose}>
          {refreshModal.currentModal}
        </Modal>
      )}

      <main className={classes['container']}>
        <Backward />

        <Reports
          loading={loading}
          authorized={reports.authorized}
          payments={reports.results}
          count={reports.count}
          current={current}
          update={update}
        />

        <Button onClick={createModal.showCurrentModal} />
      </main>
    </>
  )
}
