import { useState } from 'react'

export function useParams(initialParams = []) {
  const [params, setParams] = useState(initialParams)

  const update = ({ newParams }) => setParams(newParams)

  const findByName = (name) => {
    const index = params.findIndex((item) => item.name === name)
    if (index < 0) return { find: false, index }

    return { find: true, index }
  }

  const toggleSelection = (name) => {
    const { find, index: position } = findByName(name)
    if (!find) return

    params[position].selected = !params[position].selected
    setParams([...params])
  }

  const restart = () => {
    const cleanedParams = params.map((param) => ({
      ...param,
      selected: false
    }))
    setParams(cleanedParams)
  }

  return {
    params,
    update,
    findByName,
    toggleSelection,
    restart
  }
}
