import Card from '../../Card/Card'
import classes from './Results.module.css'

export default function Results({ games, loading }) {
  const searchedGames = (game) => <Card key={game.id} game={game} />

  if (loading)
    return (
      <div className={`${classes['container']}`}>
        <h2 className={`${classes['result']}`}>Loading...</h2>
      </div>
    )

  if (!games.length)
    return (
      <div className={`${classes['container']}`}>
        <h2 className={`${classes['result']}`}>No results...</h2>
      </div>
    )

  return (
    <section className={classes['container']}>
      <div className={classes['cards']}>{games.map(searchedGames)}</div>
    </section>
  )
}
