import { useHistory } from 'react-router-dom'

import './Card.css'

export default function Card({ game, searchById = () => {} }) {
  const { id, title, thumbnail, creator } = game
  const { push } = useHistory()

  const onClickHandler = () => {
    push(`/games/regular/${id}/${title.replace(' ', '_')}`)
    searchById(id)
  }

  return (
    <article className="card-container" onClick={onClickHandler}>
      <img
        className="game-thumbnail"
        src={thumbnail}
        alt={`game thumbnail ${game.title}`}
      />
      <h2 className="game-title">{title}</h2>
      <h3 className="game-creator">{creator}</h3>
    </article>
  )
}
