import { useState, useContext } from 'react'
import { useHistory } from 'react-router-dom'

import UserContext from 'context/userContext'

import Modal from '../../Modal/Modal'
import classes from './Price.module.css'

export default function Price(props) {
  const { id, discount, basePrice, buyGame, removeUser } = props
  const { refreshModal, loginModal } = props

  const { push } = useHistory()
  const user = useContext(UserContext)

  const onSubmitHandler = async () => {
    if (!user?.user) {
      loginModal.showCurrentModal()
      return
    }

    if (!id) {
      return
    }

    const response = await buyGame({ id, token: user.user?.token })
    const { canRefresh = null } = response

    if (canRefresh == null) {
      push(response.links.href || 'game-not-found')
      return
    }

    if (canRefresh) {
      refreshModal.showCurrentModal()
      return
    }

    removeUser()
    alert('Your session has expired')
    push('/')
  }

  return (
    <section className={classes['container']}>
      {loginModal.activeModal && (
        <Modal onClose={loginModal.onClose}>{loginModal.currentModal}</Modal>
      )}

      {refreshModal.activeModal && (
        <Modal onClose={refreshModal.onClose}>
          {refreshModal.currentModal}
        </Modal>
      )}

      <article className={classes['price']}>
        <div className={classes['discount']}>
          <p>Discount</p>
          <span>{discount * 100}%</span>
        </div>

        <div className={classes['real-price']}>
          <h3>Digital price</h3>

          <p>This price includes the percentage discount</p>

          <span>${(basePrice * (1 - discount)).toFixed(2) * 1}</span>

          <button onClick={onSubmitHandler} className={classes['btn']}>
            buy digital
          </button>
        </div>
      </article>
    </section>
  )
}
