import { useState, useEffect } from 'react'
import { modalTypes } from '../components/NavBar/helper'

export const useModal = (initialType = '') => {
  const [activeModal, setActiveModal] = useState(false)
  const [type, setType] = useState('')

  const onClose = () => setActiveModal(false)

  const setInitialChildren = () => {
    const initialModal = modalTypes[initialType] ?? modalTypes.default

    return initialModal.modal({ onModalHandler: onChange, closeModal: onClose })
  }

  const onChange = (newType) => {
    if (newType === type) setActiveModal(true)
    setType(newType)
  }

  const showCurrentModal = () => setActiveModal(true)

  const [current, setCurrent] = useState(setInitialChildren())

  useEffect(() => {
    if (!type) return

    const currentModal = modalTypes[type] ?? modalTypes.default
    setCurrent(
      currentModal.modal({ onModalHandler: onChange, closeModal: onClose })
    )

    setActiveModal(true)
  }, [type])

  return {
    currentModal: current,
    activeModal,
    onClose,
    onChange,
    showCurrentModal
  }
}
