import classes from './Description.module.css'
import Information from './Information/Information'

export default function GameDescription(props) {
  const { title, platforms, creator, description } = props
  const concatPlatforms = (acc, current) => `${acc}, ${current}`

  return (
    <section className={classes['information']}>
      <h1 className={classes['title']}>{title}</h1>

      <Information subtitle="Creator" information={creator} />

      <Information
        subtitle="Platforms"
        information={platforms.reduce(concatPlatforms)}
      />

      <p className={classes['subtitle-description']}>{description}</p>
    </section>
  )
}
