import { useState, useEffect } from 'react'
import { homeService } from '../services/homeService'

export function useRecommended(initialId) {
  const [id, setId] = useState(initialId)
  const [recommendedGames, setRecommendedGames] = useState([])
  const [loading, setLoading] = useState(false)

  const saveRecommended = ({ similarGames, success }) => {
    if (!success) {
      setLoading(false)
      return
    }

    setRecommendedGames(similarGames)
    setLoading(false)
  }

  const showError = (error) => {
    console.log({ error })
    setLoading(false)
  }

  useEffect(() => {
    setLoading(true)
    const fetchRecommendations = () => {
      homeService.recommendedById(id).then(saveRecommended).catch(showError)
    }

    fetchRecommendations()
  }, [])

  return { recommendedGames, loading }
}
