import { useState } from 'react'

export const useInput = (type = 'text') => {
  const [value, setValue] = useState('')

  const onChange = ({ target }) => {
    const types = {
      file: () => setValue(target.files[0]),
      text: () => setValue(target.value),
      default: () => setValue(target.value)
    }

    const fieldType = types[type] ?? types.default
    fieldType()
  }

  const onReset = () => setValue('')

  return {
    type,
    value,
    onChange,
    reset: onReset
  }
}
