import Card from '../../Card/Card'
import classes from './Recommendations.module.css'

export default function ShowRecommendations({ games, loading, searchById }) {
  const recommendationsCard = (game) => (
    <Card key={game.id} game={game} searchById={searchById} />
  )

  if (loading) return <div>Loading...</div>

  if (!games.length) return <div>No recommendations...</div>

  return (
    <div className={classes['cards']}>{games.map(recommendationsCard)}</div>
  )
}
