import axios from 'axios'
const BASE_URL = '/api/v1/'

export const userServices = {
  login: ({ username, password }) => {
    return axios
      .post(`${BASE_URL}user/login`, { username, password })
      .then((response) => response.data)
      .catch((error) => {
        console.log({ error })
        return error.response.data
      })
  },

  register: (payload) => {
    return axios
      .post(`${BASE_URL}user/signin`, payload)
      .then((response) => response.data)
      .catch((error) => error.response.data)
  },

  reports: ({ token, limit, offset }) => {
    const config = { headers: { Authorization: `Bearer ${token}` } }
    return axios
      .get(`${BASE_URL}payment/?limit=${limit}&offset=${offset}`, config)
      .then(({ status, data }) => ({ status, data }))
      .catch((error) => ({
        status: error.response.status,
        data: error.response.data
      }))
  },

  recoverPassword: ({ field, responses }) => {
    return axios
      .post(`${BASE_URL}user/request-password`, { field, responses })
      .then((response) => response.data)
      .catch((error) => error.response.data)
  },

  recoverPasswordHandler: ({ newPassword, confirmedPassword, token }) => {
    const config = {
      headers: { request: token }
    }

    return axios
      .put(
        `${BASE_URL}user/reset-password`,
        { newPassword, confirmedPassword },
        config
      )
      .then((response) => response.data)
      .catch(({ response }) => response.data)
  },

  createNewGame: ({ data, token }) => {
    return axios
      .post(`${BASE_URL}game`, data, {
        headers: {
          'Content-Type': 'multipart/form-data',
          Authorization: `Bearer ${token}`
        }
      })
      .then((response) => response.data)
      .catch((error) => {
        return error.response.data
      })
  },

  refreshToken: (token) => {
    const config = { headers: { Authorization: `Bearer ${token}` } }
    return axios
      .get(`${BASE_URL}user/refresh`, config)
      .then((response) => response.data)
      .catch((error) => {
        console.log({ error })
        return error?.response?.data || null
      })
  }
}
