export const headers = {
  title: 'create new game',
  subtitle: 'Create a new game to get new users'
}

export const populateData = (data) => {
  const { creator, title, description, thumbnail, price, discount } = data
  const { genders, platforms } = data
  const gameData = new FormData()
  gameData.append('title', title.value)
  gameData.append('description', description.value)
  gameData.append('creator', creator.value)
  gameData.append('basePrice', price.value)
  gameData.append('discount', discount.value)
  gameData.append('image', thumbnail.value, thumbnail.value.name)

  platforms.params.forEach((platform) => {
    if (platform.selected) gameData.append('platforms', platform.name)
  })
  genders.params.forEach((gender) => {
    if (gender.selected) gameData.append('genders', gender.name)
  })

  return gameData
}
