import { useContext } from 'react'
import { Redirect, useHistory } from 'react-router-dom'

import Header from '../Assets/Header/Header'
import Button from '../Assets/Button/Button'

import UserContext from 'context/userContext'

import { headers } from './helper'
import classes from './Refresh.module.css'

export default function Refresh({ closeModal }) {
  const { push } = useHistory()
  const { removeUser, refreshUser } = useContext(UserContext)

  const onDetachHandler = (e) => {
    e.preventDefault()
    removeUser()
    push('/')
  }

  const onRefreshHandler = (e) => {
    try {
      e.preventDefault()
      refreshUser()
      closeModal()
    } catch (error) {
      push('/')
    }
  }

  return (
    <>
      <Header {...headers} />

      <form className={classes['form']}>
        <Button title="SI" type="fill" onClick={onRefreshHandler} />
        <Button title="NO" type="outline" onClick={onDetachHandler} />
      </form>
    </>
  )
}
