import { useHistory } from 'react-router-dom'
import classes from './Numbers.module.css'

export default function Numbers({ update, count = 0, active }) {
  const countArr = [...Array(count).keys()].map((number) => number + 1)
  const { push } = useHistory()
  const onClickHandler = (number) => {
    update(number - 1)
    push({ search: `offset=${number - 1}` })
  }

  return (
    <ul className={classes['pages']}>
      {countArr.map((number) => {
        return (
          <li
            className={`${classes['item']} ${
              active + 1 === number ? classes['active'] : ''
            }`}
            key={number}
            onClick={() => onClickHandler(number)}
          >
            {number}
          </li>
        )
      })}
    </ul>
  )
}
