import Card from '../../components/Card/Card'

import './GameSection.css'

export default function GameSection({ title, games }) {
  const mapToCard = (name) => <Card key={name.id} game={name} />

  return (
    <article className="game-section">
      <h1 id={title} key={title} className="game-section-title">
        {title}
      </h1>

      <div className="game-section-cards">{games.map(mapToCard)}</div>
    </article>
  )
}
