import classes from './Game.module.css'
import Description from './Description/Description'

export default function Game({ game }) {
  if (!Object.entries(game).length) return <></>

  const { title, creator, thumbnail, platforms, description } = game

  const mainStyle = {
    backgroundImage: `linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)),  url(${thumbnail})`,
    backgroundRepeat: 'no-repeat',
    backgroundSize: 'cover',
    filter: 'blur(5px)',
    boxShadow: 'none'
  }

  return (
    <main className={classes['container']}>
      <div className={classes['img-bg']} style={mainStyle}></div>

      <div>
        <img
          className={classes['img']}
          src={thumbnail}
          alt={`thumbnail ${title}`}
        />
      </div>

      <Description
        title={title}
        platforms={platforms}
        creator={creator}
        description={description}
      />
    </main>
  )
}
