import { Link } from 'react-router-dom'
import './Genders.css'

const GENDERS = ['action', 'survival', 'adventures', 'horror']

export default function Genders(props) {
  const { genders = GENDERS } = props
  return (
    <nav className="navbar-gender">
      {genders.map((gender) => (
        <Link key={gender} className="navbar-gender-anchor" to={`/#${gender}`}>
          {gender}
        </Link>
      ))}
    </nav>
  )
}
