import axios from 'axios'
const BASE_URL = '/api/v1/'

export const homeService = {
  getAll: () => {
    return axios
      .get(`${BASE_URL}game`)
      .then((response) => response.data)
      .catch((error) => console.log({ error }))
  },

  getOne: (id) => {
    return axios
      .get(`${BASE_URL}game/${id}`)
      .then((response) => response.data)
      .catch((error) => console.log({ error }))
  },

  recommendedById: (id) => {
    return axios
      .get(`${BASE_URL}game/recommended/${id}`)
      .then((response) => response.data)
      .catch((error) => console.log({ error }))
  }
}
