import { Link } from 'react-router-dom'

import './Message.css'

export default function Message(props) {
  const { message = '', link, position, onClick = () => {}, title } = props

  const generateCustomLink = () => {
    if (link) return <Link to={link.to}>{link.name}</Link>

    if (!title) return <></>

    return (
      <button role="link" onClick={onClick}>
        {title}
      </button>
    )
  }

  const customLink = generateCustomLink()

  return (
    <p className={`form-message ${position}`}>
      {message} {customLink}
    </p>
  )
}
