import classes from './Recommendations.module.css'
import { useRecommended } from '../../../hooks/useRecommended'
import ShowRecommendations from './ShowRecommendations'

export default function Recommendations({ title, id, searchById }) {
  const { recommendedGames, loading } = useRecommended(id)

  return (
    <section className={classes['container']}>
      <h3 className={classes['title']}>More about {title}</h3>
      <ShowRecommendations
        loading={loading}
        games={recommendedGames}
        searchById={searchById}
      />
    </section>
  )
}
