import { useContext, useState } from 'react'
import { FaSearch } from 'react-icons/fa'
import { Link } from 'react-router-dom'
import UserContext from '../../../context/userContext'
import Button from './Button/Button'
import Logout from './Logout/Logout'

import './Header.css'

export default function Header({ onModalHandler }) {
  const { user } = useContext(UserContext)
  const [activeLogout, setActiveLogout] = useState(false)
  const showLogout = () => setActiveLogout(!activeLogout)

  return (
    <header className="header">
      <ul className="header-list">
        <li className={`header-item session-items ${user ? 'logged' : ''}`}>
          {user && (
            <>
              <div className="logged-container">
                <h2 className="logged-user" onClick={showLogout}>
                  {user.username}
                </h2>
              </div>
              {activeLogout && <Logout setActive={setActiveLogout} />}
            </>
          )}

          {!user && (
            <>
              <Button name="login" onClick={() => onModalHandler('login')} />
              <Button
                name="register"
                onClick={() => onModalHandler('register')}
              />
            </>
          )}
        </li>

        <li className="header-item header-principal">
          <h1 className="header-title principal-name">
            <Link className="header-title" to="/">
              ESASTEAM
            </Link>
          </h1>
        </li>

        <li className="header-item header-search">
          {user?.isAuthorized && (
            <Link role="button" className="btn btn-dashboard" to="/dashboard">
              dashboard
            </Link>
          )}

          {!user?.isAuthorized && (
            <Link role="button" className="btn btn-dashboard" to="#footer">
              about us
            </Link>
          )}

          <Link role="button" className="btn btn-search" to="/search">
            <FaSearch />
          </Link>
        </li>
      </ul>
    </header>
  )
}
