import { useState } from 'react'

import { useUser } from 'hooks/useUser'
import { useInput } from 'hooks/useInput'

import Button from '../Assets/Button/Button'
import Error from '../Assets/Error/Error'
import Input from '../Assets/Input/Input'
import Message from '../Assets/Message/Message'
import Success from '../Assets/Success/Success'
import Header from '../Assets/Header/Header'

import { Url } from 'utils/url.params'

export default function Recovery() {
  const token = Url('token')
  const { loading, error, recoverPasswordHandler } = useUser()
  const [success, setSuccess] = useState({ state: false, message: '' })
  const { reset: resetPassword, ...password } = useInput('password')
  const { reset: resetConfirmedPassword, ...confirmedPassword } =
    useInput('password')

  const onRecoveryHandler = async (event) => {
    event.preventDefault()

    const passwords = {
      newPassword: password.value,
      confirmedPassword: confirmedPassword.value,
      token: token
    }

    setSuccess({ state: false, message: '' })
    const newState = await recoverPasswordHandler(passwords)
    if (!newState?.state) {
      resetConfirmedPassword()
      return
    }

    setSuccess(newState)
  }

  return (
    <>
      <Header
        title="NEW PASSWORD"
        subtitle="Enter your new password and remember dont show the password with
        anywhere."
      />

      <form className="principal-form-container" onSubmit={onRecoveryHandler}>
        {error.ok && <Error message={error.message} />}
        {success.state && <Success message={success.message} />}

        <Input {...password} placeholder="Password" />
        <Input {...confirmedPassword} placeholder="Confirm Password" />

        <Button
          title={loading ? 'Loading' : 'Change password'}
          isSubmit={true}
          type="fill"
        />

        <Message link={{ name: 'Cancel', to: '/' }} position="center" />
      </form>
    </>
  )
}
