import { useState, useEffect } from 'react'
import { userServices } from '../services/userService'
import { useLocalStorage } from './useLocalStorage'

export function useReports(initialOffset = 0) {
  const { storedValue } = useLocalStorage('esaUser')

  const [reports, setReports] = useState({
    authorized: true,
    results: [],
    count: 0
  })
  const [error, setError] = useState({ ok: false, message: '' })
  const [loading, setLoading] = useState(false)
  const [queries, setQueries] = useState({
    limit: 5,
    offset: +initialOffset || 0
  })
  const [canRefresh, setCanRefresh] = useState(null)

  useEffect(() => {
    setLoading(true)

    const getReports = async () => {
      try {
        const payload = { token: storedValue.token, ...queries }
        const { status, data } = await userServices.reports(payload)

        const { canRefresh = null } = data
        if (typeof canRefresh === 'boolean') {
          setLoading(false)
          setCanRefresh(data)
          return
        }

        if (status === 401 || status === 403) {
          setReports({ authorized: false, message: 'Access denied' })
          setLoading(false)
          return
        }

        if (!data['success']) {
          setError({ ok: true, message: data.message })
          setLoading(false)
          return
        }

        setReports({
          authorized: true,
          results: data.reports,
          count: data['count']
        })
        setLoading(false)
      } catch (error) {
        console.error(error)
        setLoading(false)
        return { status: 401, message: error.message }
      }
    }

    getReports()
  }, [queries])

  const update = (offset) => {
    setQueries({ limit: 5, offset: offset })
  }

  return {
    reports,
    loading,
    error,
    current: queries.offset,
    update,
    canRefresh
  }
}
