import { Url } from '../../utils/url.params'

export const findByName = (list, name) => {
  const index = list.findIndex((item) => item.name === name)
  if (index < 0) return { find: false, index }

  return { find: true, index }
}

export const initialPlatforms = [
  { name: 'PlayStation', selected: false },

  { name: 'XBox', selected: false },

  { name: 'PC', selected: false }
]

export const initialGenders = [
  { name: 'Action', selected: false },

  { name: 'Terror', selected: false },

  { name: 'Survival', selected: false }
]

const setPrice = (price, prefix) => {
  if (!price || !prefix) return ''

  if (isNaN(Number(price))) return ''

  if (Number(price) < 0) return ''

  return `&${prefix}=${price}`
}

const update = (queries) => {
  const {
    name = '',
    minPrice,
    maxPrice,
    platforms = [],
    genders = []
  } = queries

  const newQuery = `q=${name}${setPrice(minPrice, 'mn')}${setPrice(
    maxPrice,
    'mx'
  )}${platforms.reduce(
    (acc, current) => reduceFilters(acc, current, 'pt'),
    ''
  )}${genders.reduce(
    (acc, current) => reduceFilters(acc, current, 'gd'),
    ''
  )}&limit=5`

  return newQuery
}

const reduceFilters = (acc, current, prefix) => {
  if (!current.selected) return acc

  return `${acc}&${prefix}=${current.name}`
}

export const getInitialQuery = () => {
  const title = Url('q')
  const minPrice = Url('mn')
  const maxPrice = Url('mx')
  const platforms = Url('pt', 'a')
  const genders = Url('gd', 'a')

  const query = {
    name: title,
    minPrice: minPrice,
    maxPrice: maxPrice,
    platforms: platforms,
    genders: genders,
    limit: '5'
  }

  Object.keys(query).map((key) => query[key] ?? delete query[key])
  return update(query)
}
