import classes from './ServiceResponse.module.css'

export default function ServiceResponse({ response }) {
  const { success = false, ok = false, message = '' } = response
  const type = success ? 'success' : 'error'

  if (!(success || ok)) {
    return <></>
  }

  const types = {
    success: { color: '#004ba8' },
    error: { color: '#ff5c58' }
  }

  const messageStyles = types[type] ?? {}

  return (
    <>
      <div className={classes['message']} style={messageStyles}>
        <p>{message}</p>
      </div>
    </>
  )
}
