import { useState, useEffect } from 'react'
import { homeService } from '../services/homeService'

export function useGames(initialGames = []) {
  const [games, setGames] = useState(initialGames)
  const [loading, setLoading] = useState(false)

  const saveEntries = ({ categories, success }) => {
    if (!success) {
      setLoading(false)
      return
    }

    const keys = Object.entries(categories)

    setGames(keys)
    setLoading(false)
  }

  const showError = (error) => {
    console.log({ error })
    setLoading(false)
  }

  useEffect(() => {
    setLoading(true)
    homeService.getAll().then(saveEntries).catch(showError)
  }, [])

  return { games, loading }
}
