import { useEffect, useState } from 'react'
import { useHistory } from 'react-router-dom'
import { searchServices } from '../services/searchService'

export function useQuery(initialQuery = '') {
  const [results, setResults] = useState([])
  const [query, setQuery] = useState(initialQuery)
  const [loading, setLoading] = useState(false)
  const { push } = useHistory()

  useEffect(() => {
    const fetchGames = async () => {
      setLoading(true)
      const response = await searchServices.search(query)

      if (!response['success']) {
        setLoading(false)
        setResults([])
        return
      }

      setResults(response['games'])
      setLoading(false)
    }

    fetchGames()
  }, [query])

  const setPrice = (price, prefix) => {
    if (!price || !prefix) return ''

    return `&${prefix}=${price}`
  }

  const reduceFilters = (acc, current, prefix) => {
    if (!current.selected) return acc

    return `${acc}&${prefix}=${current.name}`
  }

  const update = (queries) => {
    const { name, minPrice, maxPrice, platforms, genders } = queries

    const newQuery = `q=${name}${setPrice(minPrice, 'mn')}${setPrice(
      maxPrice,
      'mx'
    )}${platforms.reduce(
      (acc, current) => reduceFilters(acc, current, 'pt'),
      ''
    )}${genders.reduce(
      (acc, current) => reduceFilters(acc, current, 'gd'),
      ''
    )}`

    push({ search: newQuery })
    setQuery(newQuery)
  }

  return {
    results,
    loading,
    query,
    update
  }
}
