import { useContext } from 'react'
import { Redirect } from 'react-router'
import { useHistory, useLocation } from 'react-router-dom'

import UserContext from 'context/userContext'

import { useInput } from 'hooks/useInput'
import { useLocalStorage } from 'hooks/useLocalStorage'
import { useUser } from 'hooks/useUser'

import Button from '../Assets/Button/Button'
import Input from '../Assets/Input/Input'
import Message from '../Assets/Message/Message'
import ServiceResponse from '../Assets/ServiceResponse/ServiceResponse'

import './Login.css'

export default function Login({ onModalHandler, closeModal }) {
  const { storedValue } = useLocalStorage('esaUser', null)
  const { login, error, loading } = useUser()
  const { updateUser } = useContext(UserContext)
  const { reset: resetUsername, ...username } = useInput('text')
  const { reset: resetPassword, ...password } = useInput('password')
  const { location } = useHistory()

  const onRequestHandler = () => onModalHandler('forgot')
  const onCreateAccountHandler = () => onModalHandler('register')

  const onLoginHandler = async (event) => {
    event.preventDefault()

    const userInformation = {
      username: username.value,
      password: password.value
    }

    const loggedUser = await login(userInformation)

    if (!loggedUser) {
      resetPassword()
      return
    }

    updateUser(loggedUser)
    closeModal()
  }

  return (
    <>
      {storedValue && <Redirect to={location.pathname} />}

      <form className="principal-form-container" onSubmit={onLoginHandler}>
        <ServiceResponse response={error} />

        <Input placeholder="Username" {...username} />

        <Input {...password} placeholder="Password" />

        <Button
          title={loading ? 'Loading...' : 'Sign In'}
          type="fill"
          isSubmit={true}
        />

        <Message
          message=""
          title="Need help signing in?"
          position="end"
          onClick={onRequestHandler}
        />

        <Button
          title="create an account"
          type="outline"
          onClick={onCreateAccountHandler}
        />
      </form>
    </>
  )
}
