import { useState, useContext } from 'react'
import { userServices } from '../services/userService'
import { useLocalStorage } from './useLocalStorage'
import UserContext from 'context/userContext'

export function useUser() {
  const userContext = useContext(UserContext)
  const { removeKey } = useLocalStorage('esaUser', null)
  const [user, setUser] = useState(userContext['user'])
  const [error, setError] = useState({ success: false, ok: false, message: '' })
  const [loading, setLoading] = useState(false)

  const updateError = (response) => {
    if (!response['success']) {
      setError({ ok: true, message: response['message'], success: false })
      setLoading(false)
      return
    }

    setError({ ok: false, message: response['message'] || '', success: true })
    setLoading(false)
  }

  const clean = () => {
    setError({ ok: false, message: '', success: false })
    setLoading(false)
  }

  const updateUser = (response) => {
    if (!response['success']) {
      updateError(response)
      return null
    }

    const createUser = {
      token: response['token'],
      username: response['username'],
      isAuthorized: response['isAuthorized'],
      refreshToken: response['refreshToken']
    }

    setUser(createUser)
    updateError(response)

    return createUser
  }

  const showError = (error) => {
    console.error(error)
    updateError(error)
    removeKey('esaUser')
    return null
  }

  const transformRecover = (response) => {
    if (!response['success']) {
      updateError(response)
      return null
    }

    updateError(response)
    return response['token']
  }

  const validateRecover = (response) => {
    if (!response['success']) {
      updateError(response)
      return null
    }

    updateError(response)
    return { state: true, message: response['message'] }
  }

  const login = async ({ username, password }) => {
    setLoading(true)
    return userServices
      .login({ username, password })
      .then(updateUser)
      .catch(showError)
  }

  const register = async (newUser) => {
    setLoading(true)
    return userServices.register(newUser).then(updateUser).catch(showError)
  }

  const recoverPassword = async ({ field, responses }) => {
    setLoading(true)
    return userServices
      .recoverPassword({ field, responses })
      .then(transformRecover)
      .catch(({ response }) => response.data)
  }

  const logout = () => {
    clean()
    setUser(null)
    removeKey('esaUser')
  }

  const recoverPasswordHandler = async (payload) => {
    setLoading(true)
    setError({ ok: false, message: '', success: false })

    return userServices
      .recoverPasswordHandler(payload)
      .then(validateRecover)
      .catch(showError)
  }

  const createNewGame = async (payload) => {
    setLoading(true)
    setError({ ok: false, message: '', success: false })

    return userServices
      .createNewGame(payload)
      .then((response) => {
        if (!response['success']) {
          updateError(response)
          return { success: response['success'] }
        }

        updateError(response)

        return { success: response['success'] }
      })
      .catch((error) => {
        updateError(error)
        console.error(error)
        return { success: false }
      })
  }

  return {
    user,
    error,
    loading,
    login,
    logout,
    register,
    recoverPassword,
    recoverPasswordHandler,
    createNewGame
  }
}
