import { useInput } from 'hooks/useInput'
import { useParams } from 'hooks/useParams'
import { useUser } from 'hooks/useUser'
import { useLocalStorage } from 'hooks/useLocalStorage'

import Header from '../Assets/Header/Header'
import Input from '../Assets/Input/Input'
import Button from '../Assets/Button/Button'
import TextArea from '../Assets/TextArea/TextArea'

import { initialGenders, initialPlatforms } from 'pages/Search/helper'

import Prices from './Prices/Prices'
import Combos from './Combos/Combos'
import { headers, populateData } from './helper'
import classes from './Create.module.css'
import ServiceResponse from '../Assets/ServiceResponse/ServiceResponse'

export default function Create() {
  const { reset: resetCreator, ...creator } = useInput('text')
  const { reset: resetTitle, ...title } = useInput('text')
  const { reset: resetDescription, ...description } = useInput('text')
  const { reset: resetPrice, ...price } = useInput('number')
  const { reset: resetDiscount, ...discount } = useInput('number')
  const { reset: resetThumbnail, ...thumbnail } = useInput('file')

  const platforms = useParams(initialPlatforms)
  const genders = useParams(initialGenders)

  const { storedValue } = useLocalStorage('esaUser')
  const { createNewGame, error, loading } = useUser()

  const onCreateGame = async (event) => {
    event.preventDefault()
    const data = {
      title,
      description,
      creator,
      price,
      discount,
      thumbnail,
      platforms,
      genders
    }

    const gameData = populateData(data)

    const { success } = await createNewGame({
      data: gameData,
      token: storedValue.token
    })

    if (!success) return

    resetCreator()
    resetTitle()
    resetDescription()
    resetPrice()
    resetDiscount()
    resetThumbnail()
    platforms.restart()
    genders.restart()
  }

  return (
    <>
      <Header {...headers} />
      <form className={classes['form']} onSubmit={onCreateGame}>
        <ServiceResponse response={error} />

        <Input {...title} placeholder="title" />
        <Input {...creator} placeholder="Creator" />

        <TextArea rows={10} placeholder="Description" {...description} />
        <Prices price={price} discount={discount} />
        <Combos genders={genders} platforms={platforms} />

        <Input onChange={thumbnail.onChange} type={thumbnail.type} />
        <Button
          title={loading ? 'loading...' : 'create a game'}
          type="fill"
          isSubmit={true}
        />
      </form>
    </>
  )
}
