import Modal from '../../components/Modal/Modal'
import Genders from '../../components/NavBar/Genders/Genders'
import Header from '../../components/NavBar/Header/Header'
import { useModal } from '../../hooks/useModal'

export default function NavBar() {
  const { currentModal, activeModal, onClose, onChange } = useModal()

  return (
    <>
      {activeModal && <Modal onClose={onClose}>{currentModal}</Modal>}
      <Header onModalHandler={onChange} />
      <Genders />
    </>
  )
}
