import { useState, useEffect } from 'react'
import { gameServices } from 'services/gameService'
import { homeService } from '../services/homeService'

export function useGame(initialId) {
  const [id, setId] = useState(initialId)
  const [game, setGame] = useState({})
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState(false)
  const [message, setMessage] = useState({ ok: false, message: '' })

  useEffect(() => {
    setLoading(true)
    const fetchGame = async () => {
      try {
        const response = await homeService.getOne(id)
        if (!response['success']) {
          setLoading(false)
          setError(true)
          setMessage({ ok: false, message: response['message'] })
          return
        }

        setGame(response['game'])
        setLoading(false)
      } catch (error) {
        console.error({ error })
        setLoading(false)
        setError(true)
      }
    }

    fetchGame()
  }, [id])

  const searchById = (newId) => {
    setId(newId)
  }

  const buyGame = async ({ id, token }) => {
    setLoading(true)
    setError(false)
    return gameServices
      .createPayment({ id, token })
      .then((response) => {
        const { success, canRefresh = null } = response

        if (canRefresh !== null) {
          setLoading(false)
          console.log({ response })
          return response
        }

        if (!success) {
          setLoading(false)
          setMessage({ ok: false, message: response['message'] })
          return response
        }

        setLoading(false)
        setError(false)
        setMessage({ ok: true, message: '' })

        return response
      })
      .catch((error) => {
        console.error({ error })
        setLoading(false)
        setMessage({ ok: false, message: error['message'] })
      })
  }

  return {
    game,
    loading,
    searchById,
    error,
    buyGame,
    message
  }
}
