import { BsSquareFill, BsFillCheckSquareFill } from 'react-icons/bs'
import classes from './Filters.module.css'

export default function Combo({ title, options, onSelected }) {
  const radioOptions = (option) => {
    return (
      <li key={option.name} className={classes['item']}>
        {option.selected ? (
          <BsFillCheckSquareFill onClick={() => onSelected(option.name)} />
        ) : (
          <BsSquareFill onClick={() => onSelected(option.name)} />
        )}
        <p>{option.name}</p>
      </li>
    )
  }

  return (
    <div className={classes['combobox']}>
      <h3 className={classes['title']}>{title}</h3>
      <ul className={classes['options']}>{options.map(radioOptions)}</ul>
    </div>
  )
}
