import classes from './Button.module.css'

export default function Button(props) {
  const { title, type: buttonStyle = 'fill', onClick, isSubmit = false } = props
  const onClickHandler = onClick ?? (() => {})
  const customClasses = `${classes['btn']} ${classes[`btn-${buttonStyle}`]}`
  const type = isSubmit ? 'submit' : 'button'

  return (
    <button className={customClasses} onClick={onClickHandler} type={type}>
      {title}
    </button>
  )
}
