import GameSection from 'components/GameSection/GameSection'
import { useGames } from 'hooks/useGames'

export default function Games() {
  const { games, loading } = useGames()

  const transformToGame = (entry) => {
    const [title, categories] = entry

    return <GameSection key={title} title={title} games={categories.games} />
  }

  return (
    <section className="games-information">
      {loading ? <h1>Loading...</h1> : games.map(transformToGame)}
    </section>
  )
}
