import { BsSearch } from 'react-icons/bs'
import { useInput } from '../../../hooks/useInput'

import classes from './Bar.module.css'

export default function Bar({ onSubmit }) {
  const { reset: resetSearch, ...search } = useInput('text')

  const onSearchGames = (e) => {
    e.preventDefault()
    onSubmit(search.value)
    resetSearch()
  }

  return (
    <form className={classes['search']} onSubmit={onSearchGames}>
      <BsSearch className={classes['icon']} />
      <input className={classes['input']} {...search} placeholder="search" />
    </form>
  )
}
