import { useContext } from 'react'

import UserContext from 'context/userContext'

import { useInput } from 'hooks/useInput'
import { useUser } from 'hooks/useUser'

import Button from '../Assets/Button/Button'
import Input from '../Assets/Input/Input'
import Message from '../Assets/Message/Message'
import Header from '../Assets/Header/Header'
import ServiceResponse from '../Assets/ServiceResponse/ServiceResponse'

import Question from '../Question/Question'

import './Register.css'
import { headers } from './helper'
import { useLocalStorage } from 'hooks/useLocalStorage'

export default function Register({ onModalHandler, closeModal }) {
  const { updateUser } = useContext(UserContext)
  const { setValue } = useLocalStorage('esaUser')
  const user = useUser()
  const { reset: resetFirstname, ...firstname } = useInput('text')
  const { reset: resetLastname, ...lastname } = useInput('text')
  const { reset: resetEmail, ...email } = useInput('email')
  const { reset: resetPassword, ...password } = useInput('password')
  const { reset: resetUsername, ...username } = useInput('text')
  const { reset: resetPet, ...pet } = useInput('text')
  const { reset: resetHero, ...hero } = useInput('text')

  const onRegisterHandler = async (event) => {
    event.preventDefault()
    if (user.loading) {
      return
    }

    try {
      const newUser = {
        username: username.value,
        firstname: firstname.value,
        lastname: lastname.value,
        email: email.value,
        questions: [pet.value, hero.value],
        password: password.value
      }

      const savedUser = await user.register(newUser)
      if (!savedUser) return

      updateUser(savedUser)
      closeModal()
    } catch (error) {
      console.error(error)
    }
  }

  const onHaveAccount = () => onModalHandler('login')

  return (
    <>
      <Header {...headers} />

      <form className="principal-form-container" onSubmit={onRegisterHandler}>
        <Input {...firstname} placeholder="First name" />

        <Input {...lastname} placeholder="Last name" />

        <Input {...username} placeholder="Username" />

        <Input {...email} placeholder="Email" />

        <Input {...password} placeholder="Password" />

        <Message
          message="By creating an account, I agree to the"
          link={{ name: 'Terms of Use', to: '/terms' }}
        />

        <Message
          message="an acknowledge that I have read the"
          link={{ name: 'Privacy Police', to: '/privacy' }}
        />

        <Question pet={pet} hero={hero} />

        <Button
          title={user.loading ? 'Loading...' : 'Sign Up'}
          type="fill"
          isSubmit={true}
        />

        <ServiceResponse response={user.error} />

        <Message
          message="Already have a account?"
          title="Sign In"
          position="center"
          onClick={onHaveAccount}
        />
      </form>
    </>
  )
}
