import { useState } from 'react'
import { Url } from 'utils/url.params'

import Input from '../Assets/Input/Input'
import Button from '../Assets/Button/Button'
import Message from '../Assets/Message/Message'
import Header from '../Assets/Header/Header'
import Response from '../Assets/Response/Response'

import { useInput } from 'hooks/useInput'

import { gameServices } from 'services/gameService'

import { headers, INITIAL_LINK, INITIAL_MESSAGE } from './helper'
import ServiceResponse from '../Assets/ServiceResponse/ServiceResponse'

export default function Game() {
  const { reset: resetEmail, ...email } = useInput('email')
  const { reset: resetPassword, ...password } = useInput('password')

  const [link, setLink] = useState(INITIAL_LINK)
  const [message, setMessage] = useState(INITIAL_MESSAGE)
  const [loading, setLoading] = useState(false)

  const token = Url('token')

  const onBuyGame = async (event) => {
    event.preventDefault()
    setLoading(true)
    try {
      const payload = { email: email.value, password: password.value, token }

      const response = await gameServices.buyGame(payload)
      if (!response['success']) {
        setMessage({ ok: true, message: response['message'], success: false })
        setLoading(false)
        return
      }

      setMessage({ ok: false, message: response['message'], success: true })
      setLoading(false)
      setLink({ name: 'Go home', to: '/' })
    } catch (error) {
      setLoading(false)
      setMessage({ ok: true, message: error.message, success: false })
    }
  }

  return (
    <>
      <Header {...headers} />

      <form className="principal-form-container" onSubmit={onBuyGame}>
        <ServiceResponse response={message} />

        <Input placeholder="Email Address" {...email} />

        <Input {...password} placeholder="Password" />

        <Button
          title={loading ? 'loading' : 'buy game'}
          type="fill"
          isSubmit={true}
        />

        <Message link={link} position="center" />
      </form>
    </>
  )
}
