import Bar from 'components/Search/Bar/Bar'
import Combo from 'components/Search/Filters/Combo'
import Filter from 'components/Search/Filters/Filter'
import Results from 'components/Search/Results/Results'

import { useInput } from 'hooks/useInput'
import { useParams } from 'hooks/useParams'
import { useQuery } from 'hooks/useQuery'

import { getInitialQuery, initialGenders, initialPlatforms } from './helper'
import classes from './Search.module.css'

export default function Search() {
  const genders = useParams(initialGenders)
  const platforms = useParams(initialPlatforms)
  const query = useQuery(getInitialQuery())

  const { reset: minPriceReset, ...minPrice } = useInput('number')
  const { reset: maxPriceReset, ...maxPrice } = useInput('number')

  const onSubmitHandler = (name) => {
    const queries = {
      name,
      minPrice: minPrice.value,
      maxPrice: maxPrice.value,
      genders: genders.params,
      platforms: platforms.params
    }
    query.update(queries)
  }

  return (
    <main className={classes['container']}>
      <Bar onSubmit={onSubmitHandler} />

      <div className={classes['results']}>
        <Results games={query.results} loading={query.loading} />

        <div>
          <Filter minPrice={minPrice} maxPrice={maxPrice} />

          <Combo
            title="Platforms"
            options={platforms.params}
            onSelected={platforms.toggleSelection}
            type="platform"
          />

          <Combo
            title="Genders"
            options={genders.params}
            onSelected={genders.toggleSelection}
            type="gender"
          />
        </div>
      </div>
    </main>
  )
}
